.smiley {
  float: left;
  background: linear-gradient(135deg, #ffe919 0%, #fbc000 100%);
  border-radius: 100%;
  position: relative;
  width: 500px;
  height: 500px;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 30px 10px;
  margin-bottom: 50px;
  animation: shadowMove infinite 5s ease;
}
.smiley.has-beard .beard {
  transition: all 0.3s ease;
  background: rgba(0, 0, 0, 0.95);
}
.smiley.has-moustache .moustache {
  transition: all 0.3s ease;
  background: black;
}
.smiley.has-glasses .glasses.eye-glasses {
  transition: all 0.3s ease;
  display: block;
}
.smiley.has-sunglasses .glasses.sunglasses {
  transition: all 0.3s ease;
  display: block;
}
.smiley.mood-happy .eyebrow {
  display: none;
}
.smiley.mood-angry .eyebrow.left {
  transform: rotate(20deg);
}
.smiley.mood-angry .eyebrow.right {
  transform: rotate(-20deg);
}
.smiley.mood-angry .mouth {
  transform: rotate(180deg);
}
.smiley.mood-sad .mouth {
  transform: rotate(180deg);
  height: 8%;
}
.smiley.mood-sad .eyebrow.right {
  transform: rotate(20deg);
}
.smiley.mood-sad .eyebrow.left {
  transform: rotate(-20deg);
}
.smiley.mood-sad .eye.left .tear {
  display: block;
}
.smiley.mood-confused .eyebrow.right {
  transform: rotate(-10deg);
  top: 21%;
}
.smiley.mood-confused .eyebrow.left {
  transform: rotate(-40deg);
  top: 13%;
}
.smiley.mood-confused .mouth {
  height: 20%;
  width: 20%;
  left: 40%;
  border-radius: 50%;
}
.smiley.mood-calm .mouth {
  height: 8%;
}
.smiley.mood-calm .eyebrow.right {
  transform: rotate(10deg);
}
.smiley.mood-calm .eyebrow.left {
  transform: rotate(-10deg);
}
.smiley.mood-calm .eye {
  border-bottom: 12px solid #b57700;
  background: transparent;
  width: 20%;
}
.smiley.mood-calm .eye.left {
  left: 20%;
}
.smiley.mood-calm .eye.right {
  right: 20%;
}
.smiley.mood-calm .mouth {
  width: 30%;
  left: 35%;
}
.smiley.mood-disgusted .eye {
  background: transparent;
  width: 20%;
}
.smiley.mood-disgusted .eye.left {
  left: 17.5%;
  border-right: 12px solid #b57700;
  height: 20%;
}
.smiley.mood-disgusted .eye.right {
  right: 17.5%;
  border-left: 12px solid #b57700;
  height: 20%;
}
.smiley.mood-disgusted .mouth {
  transform: rotate(180deg);
}
.smiley.mood-disgusted .mouth:before {
  content: '';
  background: linear-gradient(#c80000 60%, #000000);
  position: absolute;
  top: -60%;
  left: 50%;
  margin-left: -15%;
  width: 30%;
  height: 100%;
  border-radius: 20px;
}
.smiley.mood-surprised .eyebrow.left {
  transform: rotate(-30deg);
  top: 10%;
}
.smiley.mood-surprised .eyebrow.right {
  transform: rotate(30deg);
  top: 10%;
}
.smiley.mood-surprised .mouth {
  width: 20%;
  height: 20%;
  left: 40%;
  border-radius: 100%;
}
.smiley.mood-unknown .eyebrow.left {
  animation: raise linear infinite 1s;
}
.smiley.mood-unknown .eyebrow.right {
  animation: raise linear infinite 1s;
  animation-direction: alternate;
}
.smiley.mood-unknown .mouth {
  animation: alternateMouth linear infinite 4s;
}
.eye {
  width: 14%;
  height: 14%;
  background: #b57700;
  border-radius: 100%;
  position: absolute;
  top: 27%;
}
.eye.left {
  left: 22.5%;
}
.eye.right {
  right: 22.5%;
}
.tear {
  display: none;
  position: absolute;
  left: 20%;
  top: 120%;
  width: 50%;
}
.mouth {
  position: absolute;
  width: 60%;
  height: 24%;
  top: 60%;
  left: 20%;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-left-radius: 100px;
  border-bottom-right-radius: 100px;
  backface-visibility: hidden;
  box-sizing: border-box;
  background: #b57700;
  transition: all 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
  z-index: 3;
  transform: rotate(0deg);
}
.beard {
