.moustache.left {
  right: 51%;
  transform: rotate(-8deg);
}
.moustache.right {
  left: 51%;
  transform: rotate(8deg);
}
.glasses {
  position: absolute;
  width: 80%;
  left: 10%;
  height: 24%;
  top: 24%;
  display: none;
}
.glasses .g-top {
  position: absolute;
  height: 15px;
  width: 110%;
  background-color: #3c3c3c;
  top: 0%;
  left: -5%;
  border-radius: 5px;
}
.glasses .glass {
  position: absolute;
  height: 100%;
  width: 36%;
  background-image: linear-gradient(to bottom, rgba(77, 226, 220, 0.5), rgba(56, 136, 209, 0.5) 70%);
  border: 10px solid #3c3c3c;
  top: 0;
}
.glasses .g-left {
  left: 5%;
  transform: rotate(2deg);
  border-radius: 12px 12px 33px 29px;
}
.glasses .g-right {
  right: 5%;
  transform: rotate(-2deg);
  border-radius: 12px 12px 29px 33px;
}
.glasses.sunglasses:after {
  content: '';
  position: absolute;
  left: 50%;
